@import url(https://fonts.googleapis.com/css?family=Poppins:200,400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.photoGridExample {
    font-family: Poppins;
    color: black;
}

.photoGrid {
    display: grid;
    grid-gap: 6rem;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    grid-auto-rows: calc(150vh / 2 - 6rem);
    justify-items: center;
    margin: 5rem;
}

.photoGridTitle {
    font-size: 1rem;
    position: absolute;
    left: 33%;
    margin-top: 0vh;
}

.photoGridSquare {
    cursor: pointer;
    width: 100%;
    height: 100%;
    position: relative;
}

.photoGridShader {
    background-image: -webkit-linear-gradient(hsla(0, 0%, 100%, 0.3), white);
    background-image: linear-gradient(hsla(0, 0%, 100%, 0.3), white);
    position: absolute;
    bottom: -5px;
    left: 0;
    right: 0;
    top: 0;
    opacity: 1;
    z-index: 1;
}

.photoGridImg {
    width: 100%;
    height: 100%;
    position: relative;
    will-change: transform;
}

.photoGridSquareExpanded {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    cursor: pointer;
}

.photoGridSquareExpanded .photoGridImg {
    width:100%;
    height: 100%;
    will-change: transform;
}

.photoGridContentContainer {
    width: 830px;
    margin: auto;
    position: absolute;
    top: 8vh;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    font-size: 1.6rem;
    line-height: 1.6;
    z-index: 4;
}

.photoHeading {
    position: relative;
    z-index: 2;
    font-size: 1.5rem;
    margin: 1rem 0;
    color : #fff
}

.photoHeadingFocused {
    font-size: 2rem;
    font-weight: 900;
    margin: 1rem 0;
}

.photoGridShaderHidden {
    opacity: 0;
}

.photoGridExample [data-fade-in] {
    opacity: 0;
}

.photoGridLead {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
}

.photoGridFocused {
    position: absolute;
    top: 0;
    width: 100%;
}
